import classnames from 'classnames';
import { css } from 'emotion';
import React from 'react';
import core from '../core/Core';
import data from '../core/Data';
import EventEmitter, { Event } from '../core/EventEmitter';
import fbase from '../fb/fbase';


let uniqCnt = 0;
const selfName = "WMain";
const WMain = () => {
    const course1 = data.leftJSON.map((item1: any, i: number) => {
        return <CourseItem json={item1} key={uniqCnt++} />
        // return <Course json={item1} key={uniqCnt++} />
    });
    return (
        <div className="WMain">
            <div className="notice">
                .    .<br></br>
                      <br></br>   . <br></br>
                     . <br></br>
                <a href="mailto:dev@casete.in">dev@casete.in</a>
            </div>

            <ul className="course-list">
                {course1}
                {/* {cid && <WCourse json={courseItemListJSON[0]} parentName={selfName} />} */}
            </ul>
            {/* {course1} */}
        </div>
    );
};

const CourseItem = ({
    json
}: {
    json: any
}) => {
    const isOwner = fbase.userMoreInfo.buyCourses.includes(json.courseID);

    return (
        <>

            <li className="course-item">
                <div className={classnames(["cover-img", css`
                background-image: url("${data.getRootPath()}resource/courses/${json.courseID}/cover.jpg");
            `])}>
                </div>
                <div className="info">
                    <h3>{json.title}</h3>
                    <div className="info-sub">
                        <div className="course-num">{json.item.length} </div>
                        <Tags str={json.tags} key={json.courseID} />
                        {
                            isOwner
                                ?
                                <div className="btns-func">
                                    <button onClick={() => {
                                        core.setParameter(json.query);
                                    }}>
                                        
                            </button>
                                </div>
                                :
                                <div className="btns-func">
                                    <button className="btn btn-border" onClick={() => {
                                        core.setParameter(json.query);
                                    }}> </button>
                                    <button className="btn btn-border" onClick={() => {
                                        // core.showBuyMessage({
                                        //     courseID: json.courseID
                                        // });
                                        if (fbase.isLogin()) {
                                            core.buyProduct({
                                                courseID: json.courseID
                                            })
                                        } else {
                                            core.showToastMessage({
                                                message: "  .",
                                                mtime: 3000
                                            })
                                            EventEmitter.dispatch(Event.FB_START_LOGIN, {
                                                from: "buyProduct",
                                                courseID: json.courseID,
                                            });
                                        }

                                    }}> 1,300</button>
                                </div>
                        }
                    </div>


                </div>
            </li>
        </>

    );
}

export const Tags = ({
    str
}: {
    str: string
}) => {
    const arr = str.split(',');
    let Tag = arr.map((item: string, i: number) => {
        return <span className="tag" key={i}>#{item}</span>
    });
    if (str === '') {
        Tag = [];
    }
    return (
        <div className="Tags">
            {Tag}
        </div>
    );
}

export default WMain;