import classnames from 'classnames';
import { padStart as _pad } from 'lodash';
import React from 'react';
import core from '../core/Core';
import EventEmitter, { Event } from '../core/EventEmitter';
import fbase from '../fb/fbase';

const WCourseItem = ({
    viewType,
    itemJSON,
    courseID,
    idx,
}: {
    viewType: string,
    itemJSON: any,
    courseID: string,
    idx: number,
}) => {
    // console.log('WCourseItem',viewType,itemJSON,idx);
    if (viewType === 'movie') {
        console.log('WCourseItem', itemJSON.tit);
    }
    const isAvailableBook = fbase.isAvailableBook(courseID);


    return (
        <>
            {
                viewType === 'movie'
                    ?
                    <CItemMovie isAvailableBook={isAvailableBook} courseID={courseID} itemJSON={itemJSON} idx={idx} key={'CItemMovie' + idx} />
                    :
                    <CItem courseID={courseID} itemJSON={itemJSON} idx={idx} key={'CItem' + idx} />
            }
            {/* <CItem itemJSON={itemJSON} idx={idx} key={idx} /> */}
        </>
    );
};
const CItem = (
    {
        itemJSON,
        courseID,
        idx
    }: {
        itemJSON: any,
        courseID: string,
        idx: number
    }) => {
    return (
        <li className="CourseItem" onClick={() => {
            core.setParameter(itemJSON.query);
        }} key={idx}>{itemJSON.tit}</li>
    );
}
const CItemMovie = (
    {
        itemJSON,
        courseID,
        isAvailableBook,
        idx
    }: {
        itemJSON: any,
        courseID: string,
        isAvailableBook: string,
        idx: number
    }) => {
    const Tits = itemJSON.tit.map((str: string, i: number) => {
        return <div key={i} className="sentense">{i + 1}. {str}</div>
    })
    const idxPad = _pad(String(idx + 1), 3, '0');
    const isTrial = itemJSON.isTrial === 'true';
    return (
        <li className="CourseItem movie" onClick={() => {
            // alert(itemJSON.query);

            if (!isTrial && isAvailableBook !== 'available') {
                // alert('  .    .')
                if (process.env.NODE_ENV === 'development') {
                    EventEmitter.dispatch(Event.SHOW_ALERT, {
                        moduleName: "AlertBuyProduct",
                        courseID: courseID,
                    });
                }

                return;
                if (isAvailableBook === 'needLogin') {
                    core.showToastMessage({
                        message: "  .",
                        mtime: 3000
                    });
                    EventEmitter.dispatch(Event.FB_START_LOGIN, {
                        from: "buyProduct",
                        courseID: courseID,
                    });
                } else if (isAvailableBook === 'needPurchase') {
                    // alert('  .    .')
                    EventEmitter.dispatch(Event.SHOW_ALERT, {
                        moduleName: "AlertBuyProduct",
                        courseID: courseID,
                    });
                }
                return;
            }
            core.setParameter(itemJSON.query);
        }} key={idx}>

            <div className={classnames(["group-img", { trial: isTrial }])}>
                {isTrial && isAvailableBook !== 'available' && <div className="mark-trial">
                    
                </div>}
                <img className={classnames(["img", { trial: isTrial }])} src={`/resource/courses/${courseID}/thumb/a${idxPad}.jpg`}></img>
                <div className="sceneno">
                    <div className="t1">DAY</div>
                    <div className="t2">{idxPad}</div>
                </div>
            </div>

            <div className="sentenses">{Tits}</div>

        </li>
    );
}
export default WCourseItem;